import React from 'react'
import Search from '../../../components/search'
import axios from 'axios'
import './index.css'
import qs from 'querystring'
export default function SearchPage(props) {
    const [hasReq,setHasReq]=React.useState(false)
    const [bfr,setBfr]=React.useState('')
    const [weather,setWeather]=React.useState('')
    const [city,setCity]=React.useState('')
    const [remark,setRemark]=React.useState('')
    // const {tips}=props.match.params  
    const search=props.location.search
    const {tips,key}= qs.parse(search.slice(1))
    // const {tips}=props.location.state
    // console.log(renderResult);
    let onSearch=''
    switch (key){
        case 'bfr':
         onSearch=(searchRef)=>{
            const value=searchRef.current.value
            const valueArr=value.split('，')
            const waistline=(valueArr[2]*1)/2-11
            
                    axios.get(`http://localhost:4000/api1/bfr?waistline=${waistline}&sex=${valueArr[0]}&weight=${valueArr[1]}`).then(
                        res=>{
                            console.log(res);
                            setBfr(res.data.result.bfr)
                            setRemark(res.data.result.remark)
                            searchRef.current.value=''
                        },err=>{
                            console.log(err);
                        })
            }
        break
        case 'weather':
            onSearch=(searchRef)=>{
                const value=searchRef.current.value
                // 在这里做了urlencode，链接到了后端被parse之后会重新变成中文，所以最好在后端处理数据
                // const valueCode=encode(value)
                        axios.get(`http://localhost:4000/api1/weather?city=${value}`).then(
                            res=>{
                                setWeather(res.data.result.future)
                                setCity(value)
                                searchRef.current.value=''
                            },err=>{
                                console.log(err);
                            })
                }
            break
default:
    break
    }
    
    return (
        <div>
            <Search onSearch={onSearch}/>
            <div className='search-tips'>提示：{tips}</div>
            {
                key==='bfr'?
                <div className='search-result'>
                        <ul>
                            <li>体脂率：{bfr}</li>
                            <li>体脂率评价：{remark}</li>
                        </ul>
                    </div>:
                    ''
            }
            {
                 key==='weather'?
                 <div className='search-result'>
                     {city}天气
                     <ul>
                       {
                           weather!==''?
weather.map((weatherItem,index)=>{
    return (
    <li key={weatherItem.date}>
     <br></br>日期：{weatherItem.date}--风向：{weatherItem.direct}<br />温度：{weatherItem.temperature}--天气状况：{weatherItem.weather}
    </li>
    )

}):''
                       }
                       </ul>
                     </div>
                     :''
            }
            {/* {
                key==='bfr'?
            <div className='search-result'>
                    <ul>
                        <li>体脂率：{bfr}</li>
                        <li>体脂率评价：{remark}</li>
                    </ul>
                </div>:''
}

{
     key==='weather'?
     <div className='search-result'>
             <ul>
                 <li>天气</li>
             </ul>
         </div>:''
} */}
        </div>
    )
}
